.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.header {
  height: 20px;
  color: white;
  margin-bottom: 30px;
}


.presentation-container {
  margin-top: 30px;
  background-color: hsla(220, 12%, 95%);
}

.App-intro {
  font-size: large;
}

.introduction {
  padding-top: 12px;
  padding-left: 12px;
  font-family: "Consolas", "Courier New", "Courier";

}

.title, .presentation-container{
  font-family: "Consolas", "Courier New", "Courier";
}


.icons .fa{
  padding: 10px;
}

.fa-envelope-o{
  padding-left: 5px;
}

.fa-linkedin-square, .fa-github, .fa-wordpress{
  color: grey;
}


.fa-linkedin-square:hover{
  color: #0077B5;
}

.fa-github:hover {
  color: black;
}
.fa-wordpress:hover{
  color: #21759b;
}

.app-picture {
  border-radius: 50%;
  vertical-align: middle;
}

@media screen and (max-width: 700px) {
  .wrapper {
    margin-left: 5px;
  }
}


